import React from 'react'
import { Link } from 'react-router-dom'
import { FaReact } from 'react-icons/fa';


function Nav() {
    const navStyle = {
        color: 'white'
    }
    const navSlide = () => {
        let burger = document.querySelector('.burger')
        let navX = document.querySelector('.navLinks')
        // const navLinks = document.querySelectorAll('.navLinks li')
        console.log(burger)
        burger.addEventListener('click', function () {
            navX.classList.toggle('navActive')

            // navLinks.forEach((link, index) => {
            //     if (link.style.animation) {
            //         link.style.animation = ''
            //         // console.log(index / 7)

            //     }
            //     else {
            //         link.style.animation = `navLinkFade 0.5s ease forwards ${index / 9 + .1}s`
            //         // console.log(index / 7)

            //     }
            // })
        })
        // burger.classList.toggle("toggle");
    }


    return (
        <nav className="navApp">
            <Link style={navStyle} to='/'>
                <div>
                    <h3 className="logoIcon"> React APP</h3>
                    <FaReact className="testt" />
                </div>
            </Link>
            <ul className="navLinks">
                <Link style={navStyle} to='/IsometricCSS/IsometricCSS'>
                    <li>isometric CSS</li>
                </Link>
                <Link style={navStyle} to='/Currency/CurrencyApp'>
                    <li>Prevod</li>
                </Link>
                <Link style={navStyle} to='/TicTacToe/TicTacToe'>
                    <li>TicTacToe</li>
                </Link>
                <Link style={navStyle} to='/Todo/TodoApp'>
                    <li>Todo</li>
                </Link>
                <Link style={navStyle} to='/Recepty/Recepty'>
                    <li>Recepty</li>
                </Link>
                <Link style={navStyle} to='/ReceptyV2/ReceptyV2'>
                    <li>ReceptyV2</li>
                </Link>
                <Link style={navStyle} to='/ExpenseTracker/ExpenseTrackerApp'>
                    <li>Expense Tracker</li>
                </Link>
                <Link style={navStyle} to='/Covid/CovidApp'>
                    <li>Covid - 19</li>
                </Link>

            </ul>
            <div className="burger" onClick={navSlide}>
                <div className="line1"></div>
                <div className="line2"></div>
                <div className="line3"></div>
            </div>

        </nav>
    )
}

export default Nav
